@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
  margin: 0 auto;
}
*,
*::after,
*::before {
  box-sizing: inherit;
}
body {
  font-family: "Open Sans", sans-serif;
  margin: 0;
}
.visually-hidden:not(:focus):not(:active),
input[type="radio"].visually-hidden,
input[type="checkbox"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  width: 100%;
}
.product-week {
  background: url("../img/bg-zigzag-line.svg") -5% 100% no-repeat;
  background-size: 100%;
  padding-bottom: 10px;
}
.product-week__list {
  margin: 0;
  padding: 0;
  display: table;
}
.product-week__row {
  display: table-row;
}
.product-week__cell {
  display: table-cell;
}
.product-week__option {
  padding-right: 31px;
}
.about {
  padding-top: 10px;
}
.reviews {
  padding-bottom: 20px;
  background: url("../img/bg-zigzag-line.svg") -5% 100% no-repeat;
  background-size: 100%;
}
.contacts__term {
  width: 48px;
  margin-right: 31px;
  font-size: 15px;
}
.contacts__definition {
  text-align: left;
  margin: 0;
}
.contacts__map-wrapper {
  height: 457px;
  background: url("../img/icon-map-pin.svg") 55% 40% no-repeat, url("../img/map-mobile@1x.jpg") no-repeat;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .contacts__map-wrapper {
    background: url("../img/icon-map-pin.svg") 55% 40% no-repeat, url("../img/map-mobile@2x.jpg") no-repeat;
    background-size: auto, 100%;
  }
}
.contacts__list {
  margin: 0;
  padding: 0;
  display: table;
  padding-bottom: 20px;
}
.contacts__row {
  display: table-row;
}
.contacts__cell {
  display: table-cell;
}
.contacts__option {
  padding-right: 32px;
}
.page-main__header {
  padding: 76px 0 74px 0;
}
.page-main__header--index {
  padding: 76px 0 120px 0;
  background: url("../img/hero-mobile@1x.jpg") center no-repeat;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-main__header--index {
    background: url("../img/hero-mobile@2x.jpg") center no-repeat;
    background-size: 200%;
  }
}
